#results {
    display: flex;
    flex-flow: row wrap;
}

.resultCard {
    width: 100%;
    margin: auto;
}

.resultCard a {
    color: #36618f;
}

.resultCard img {
    width: 15em;
    height: 18em;
    object-fit: cover;
    object-position: center center;
}

.search{
    position: relative;
    font-size: 200%;
    left: 5%;
}

.pdf{
    position: absolute;
    left: 90%;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
}

.image{
    position: relative;
    width: 15em;
    height: 100%;
    text-align: center;
    justify-content: flex-start;
    font-size: 100%;
    
}

.party{
    position: absolute;
    width: 400px;
    height: 56px;
    font-size: calc(1.0rem + 1vw);
    color:black;
    margin-left:50%;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    top:10%;
}
.name{
    position: absolute;
    text-align: center;
    justify-content:space-around ;
    font-size: 200%;
    left: 19%;
    width: 25%;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    bottom: 75%;
    
}
.secondname{
    font-size: calc(1.2rem + 1vw);
    position: absolute;
    text-align: center;
    justify-content: center;
    right: 35%;
    top: 30%;
    width: 50%;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
}

.phone{
    position: absolute;
    width: 350px;
    height: 82px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: calc(1.2rem + 1vw);
    line-height: 56px;
    left: 21%;
    bottom: 25%;
    color: #000000; 
}


.link{
    position: absolute;
    width: 40%;
    height: 56px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: calc(1.2rem + 1vw);
    line-height: 56px;
    left: 40%;
    bottom: 33%;
    color: #000000;
}

.website{
    position: absolute;
    justify-content: flex-end;
    width: 3%;
    height: 40%;
    right: 5%;
    bottom: 18%;
    transform: scale(3);
  
}
@media only screen and (max-width: 1366px) {
    .search{
        position: relative;
        font-size: 100%;
    }
    .pdf{
        position: absolute;
        left: 75%;
        width: 25%;
    }
    .image{
        position: -webkit-sticky;
